
import dateFormat from 'vux/src/tools/date/format.js'

import '../../assets/js/clipboard'
import api from '../../assets/js/api'
import {getParams,weChatPay} from '../../assets/js/util'
import Nodata from '../../components/nodata.vue'
export default {
components: {
Nodata
},
filters: {
dateFormat
},
data () {
return {
order_id: getParams()['order_id'] || '',
detail: {}
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(text){
this.$vux.loading.show({
text: text || ''
})
},
hideLoading(){
this.$vux.loading.hide()
},
copy(){
var clipboard = new Clipboard('button');
clipboard.on('success', e => {

this.layer('');
});
clipboard.on('error', e => {

this.layer('');
});
},

changeOrderStatus(status){
this.showLoading('')
this.$http.post(api.changeOrderStatus,{
ordersn: this.detail.ordersn,
status: status
}).then(res => {
this.hideLoading()
this.layer('')
this. getOrderDetail()
}).catch(e => {
this.hideLoading()
})
},
gotoViewCer(id){
this.$router.push({
path: `/viewcer?order_id=${id}`
})
},
/**/
gotoWeChatPay(){
location.href = weChatPay(this.detail.ordersn,this.detail.delivery)
},
/**/
uploadVoucher(){
this.$router.push({
path: `/offinePay?order_id=${this.detail.ordersn}&delivery=${this.detail.delivery}`
})
},
getOrderDetail(){
this.showLoading('')
this.$http.post(api.orderDetail,{
ordersn: this.order_id
}).then(res => {
this.hideLoading()
this.detail = res.data
}).catch(e => {
this.hideLoading()
})
}
},
mounted() {
this.copy();
this.getOrderDetail();

document.getElementsByTagName('title')[0].textContent = '';
}
}



