import api from '../assets/js/api'
export default {
    name: 'UploadPhoto',
    data() {
        return {
            file: '',  // 
        }
    },
    methods: {
        layer(text){
            this.$vux.toast.text(text || 'hello', 'middle')
        },
        showLoading(text){
            this.$vux.loading.show({
                text: text || ''
            })
        },
        hideLoading(){
            this.$vux.loading.hide()
        },
        // 
        choosePhoto(event){
            let that = this;
            let target = event.currentTarget;
            this.file = target.files[0];
            // 
            let imgType = /\.jpg|\.png|\.jpeg/i;
            if (!imgType.test(this.file.name)) {
                return this.layer("");
            } else if ((this.file.size / 1024000) > 5) {
                return this.layer("5M");
            }

            // fileReader 
            if (typeof FileReader == "undefined") {
                this.layer("");
            } else {
                this.showLoading('...');
                // 
                let reader = new FileReader();
                reader.readAsDataURL(this.file);
                reader.onload = function () {
                    // base64,
                    that.$emit('getImgData', reader.result);
                    // input
                    target.value = '';
                    that.ajaxUploadApi();
                }
            }
        },
        // ajax
        ajaxUploadApi(){
            // 
            let form = new FormData();
            form.append("file", this.file);
            this.$http.post(api.uploadImage, form).then(res => {
                this.hideLoading();
                this.$emit('uploadResData', {
                    url: res.data.url,
                    type: 'success'
                });
            }).catch( e => {
                this.hideLoading();
                this.layer('');
                this.$emit('uploadResData', {type: 'fail'});
            })
        },
    }
}



