
import InfiniteLoading from 'vue-infinite-loading';
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import api from '../../assets/js/api'
import Nodata from '../../components/nodata.vue'

export default {
components: {
Tab,
TabItem,
InfiniteLoading,
Nodata
},
data () {
return {
bottomCount: 0,
type: 0,
orders: [
{
/*  */
page: 1,
data: []
},
{
/*  */
page: 1,
data: []
},
{
/*  */
page: 1,
data: []
},
{
/*  */
page: 1,
data: []
}
]
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: ''
})
},
hideLoading(){
this.$vux.loading.hide()
},
getList($state) {
this.$http.post(api.orderIndex,{
page: this.orders[this.type].page,
status: this.type
}).then(res => {
if (res.data.length) {
this.orders[this.type].data = this.orders[this.type].data.concat(res.data);
this.orders[this.type].page++;
$state.loaded();
} else {
$state.complete();
}
}).catch(e => {
$state.complete();
})
},
gotoOrderitem(id){
this.$router.push({
path: `/orderDetail?order_id=${id}`
})
},
onItemClick(index){
this.type = index
this.$nextTick(() => {
this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');
})
},

changeOrderStatus(status,order_id){
this.showLoading('')
this.$http.post(api.changeOrderStatus,{
ordersn: order_id,
status: status
}).then(res => {
this.hideLoading()
this.layer('')
/*  */
this.orders[this.type].data = [];
this.orders[this.type].page = 1;
this.$nextTick(() => {
this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');
})
}).catch(e => {
this.hideLoading()
})
},
/**/
gotoWeChatPay(order_id){
location.href = weChatPay(order_id)
},
/**/
uploadVoucher(order_id,delivery){
this.$router.push({
path: `/offinePay?order_id=${order_id}&isPost=${delivery}`
})
}
},
mounted() {

document.getElementsByTagName('title')[0].textContent = '';
}
}



